<template>
  <div class="area-manage">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">区域管理</div>
          <span class="empty"></span>
        </div>
      </div>
      <div class="add-btn">
        <Button type="default"
                @click="showDrawer('all')">
          <span class="iconfont icon-a-lianhe68"></span>
          新增
        </Button>
      </div>
      <div class="content-box">
        <div class="content-content">
          <Tree :data="allData"
                :loading="treeLoading"
                :render="renderContent"
                @on-toggle-expand="onTreeExpandToggle"></Tree>
        </div>
      </div>
      <!-- 新增 -->
      <custom-drawer ref="drawer"
                     :title="title"
                     :formValidate="formValidate"
                     :formList="formList"
                     v-model="formValue"
                     :flag="flag"
                     :id="id"
                     @confirm="confirm"></custom-drawer>
    </div>
  </div>
</template>

<script>
import {
  addArea,
  delOrg,
  EditArea,
  getAreaListAll as getAreaList,
  getNextLevelAreaList
} from '@/api/area'
import { treeTransform } from '../../libs/util'
import OrgSelect from '@/components/all-area-select'
import customDrawer from '@/components/common-drawer/common-drawer'

export default {
  name: 'area-manage',
  i18n_prefix: 'page.area-manage',
  components: {
    OrgSelect,
    customDrawer
  },

  data () {
    return {
      flag: 0,
      nodeMap: {},
      title: '',
      ids: [],
      formFilter: {
        orgName: ''
      },
      newData: [],
      oldData: [],
      formList: [],
      formValue: {},
      id: '',
      treeLoading: false,
      formValidate: {
        name: [
          {
            required: true,
            message: '区域名称不能为空',
            trigger: 'blur'
          }
        ]
      },
      searchKey: [],
      allData: [],
      buttonProps: {
        type: 'text',
        size: 'small'
      },
      type: ''
    }
  },
  methods: {
    checkFrom () {
      // let me = this;
      return new Promise((resolve, reject) => {
        this.$refs.formAdd.validate((valid, b) => {
          if (valid) {
            resolve()
          } else {
            // console.log(b)
            reject(valid, b)
          }
        })
      })
    },
    checkFrom1 () {
      // let me = this;
      return new Promise((resolve, reject) => {
        this.$refs.formEdit.validate((valid, b) => {
          if (valid) {
            resolve()
          } else {
            // console.log(b)
            reject(valid, b)
          }
        })
      })
    },
    renderContent (h, { root, node, data }) {
      let action, actionAttr
      if (node.nodeKey === 0) {
        actionAttr = {
          style: {
            width: this.$flexible(202) + 'px',
            fontSize: this.$flexible(14) + 'px'
          }
        }
        action = '操作'
      } else {
        actionAttr = {
          class: 'span-right'
        }
        action = [
          h(
            'Button',
            {
              props: Object.assign({}, this.buttonProps),
              class: 'primary',
              style: {
                marginLeft: 0,
                paddingLeft: 0,
                color: '#0084FF'
              },
              on: {
                click: () => {
                  this.showDrawer('edit', root, node, data)
                }
              }
            },
            '编辑'
          ),
          h(
            'Button',
            {
              props: Object.assign({}, this.buttonProps),
              class: 'primary',
              style: {
                color: '#0084FF'
              },
              on: {
                click: () => {
                  this.showDrawer('child', root, node, data)
                }
              }
            },
            '新增下级区域'
          ),
          h(
            'Button',
            {
              props: Object.assign({}, this.buttonProps),
              class: 'secondary',
              style: {
                color: '#F12121'
              },
              on: {
                click: () => {
                  this.remove(root, node, data)
                }
              }
            },
            '删除'
          )
        ]
      }
      return h(
        'li',
        {
          class: 'li-box',
          style: {
            display: 'flex',
            width: '100%',
            background:
              node.nodeKey % 2 === 0 ? 'rgba(0,0,0,.3)' : '#262C3A',
            zIndex: 0
          }
        },
        [
          h(
            'span',
            {
              class: 'span-title-right',
              style: {
                display: 'flex',
                alignItems: 'center'
              }
            },
            [
              h(
                'span',
                {
                  class: 'span-title',
                  style: {
                    cursor: 'pointer'
                  }
                },
                data.title
              )
            ]
          ),
          h('span', actionAttr, action)
        ]
      )
    },
    handleEidt (scope) {},
    // 编辑新增区域
    passCancel () {
      this.flag = 0
      this.modal1 = false
    },
    ok () {
      this.checkFrom().then(() => {
        this.modal1 = !this.modal1
        if (this.id) {
          EditArea({ ...this.formAdd, id: this.id }).then(
            () => {
              this.$Message.success(this.$t('common.message.edit_success'))
              let parentNode = this.nodeMap[this.formAdd.oldNode]
              this.formAdd.data.parentId = this.formAdd.parentId
              this.formAdd.data.orgName = this.formAdd.orgName
              this.formAdd.data.title = this.formAdd.orgName
              let newNode = this.nodeMap[this.formAdd.parentId]
              if (newNode) {
                newNode.children.push(this.formAdd.data)
              }

              let idx = parentNode.children.indexOf(this.formAdd.data)
              parentNode.children.splice(idx, 1)
            },
            (res) => {
              if (res.code == 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.edit_fail'))
              }
            }
          )
        } else {
          addArea(this.formAdd).then(
            (res) => {
              if (res.code === 200) {
                this.$Message.success(this.$t('common.message.add_success'))
                this.getData()
              } else if (res.code == 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.add_fail'))
              }
            }
          )
        }
      })

      // this.$Message.info("Clicked ok");
    },
    getIds (arr) {
      arr.map((item) => {
        this.ids.push(item.id)
        if (item.expand && item.hasChild > 0) {
          this.getIds(item.children)
        }
      })
    },
    cancel () {
      this.$Message.info('Clicked cancel')
    },
    // 打开模态框
    showDrawer (type, root, node, data) {
      this.$refs.drawer.open()
      this.type = type
      if (type === 'all') {
        // 添加
        this.title = '新增区域'
        this.formList = [
          {
            type: 'input',
            label: '区域名称',
            placeholder: '请输入区域名称',
            value: 'name'
          },
          {
            type: 'area-select',
            label: '上级区域',
            placeholder: '请选择区域',
            value: 'parentId'
          }
        ]
        this.formValue = {
          name: '',
          parentId: undefined
        }
      } else if (type === 'child') {
        // 添加下级
        this.title = '新增下级区域'
        this.formList = [
          {
            type: 'input',
            label: '区域名称',
            placeholder: '请输入区域名称',
            value: 'name'
          },
          {
            type: 'input',
            label: '上级区域',
            placeholder: '请选择区域',
            value: 'parent'
          }
        ]
        this.formValue = {
          name: '',
          parent: data.orgName,
          parentId: data.id
        }
      } else {
        // 编辑
        this.title = '编辑区域'
        this.formList = [
          {
            type: 'input',
            label: '区域名称',
            placeholder: '请输入区域名称',
            value: 'name'
          },
          {
            type: 'area-select',
            label: '上级区域',
            placeholder: '请选择区域',
            value: 'parentId'
          }
        ]
        console.log('---------', data)
        this.formValue = {
          name: data.orgName,
          id: data.id,
          parentId: data.parentId
        }
      }
    },
    confirm (flag) {
      if (flag) {
        switch (this.type) {
          case 'all':
            this.addAll()
            break
          case 'child':
            this.addAll()
            break
          case 'edit':
            this.edit()
            break
          default:
            break
        }
      }
    },
    // 添加全部\下级
    addAll () {
      if (!this.formValue.name) {
        return
      }
      addArea({orgName: this.formValue.name, parentId: this.formValue.parentId}).then(res => {
        if (res.code === 200) {
          this.$Message.success(this.$t('common.message.add_success'))
          this.$refs.drawer.close()
          this.formValue = {
            name: '',
            parent: ''
          }
          this.getData()
        } else {
          this.$Message.error(this.$t('common.message.add_fail'))
        }
      })
    },
    // //添加
    // addChild() {
    //   console.log('child')
    // },
    // 编辑
    edit () {
      if (!this.formValue.name) {
        return
      }
      EditArea({
        id: this.formValue.id,
        orgName: this.formValue.name,
        parentId: this.formValue.parentId
      }).then(res => {
        if (res.code === 200) {
          this.$Message.success(this.$t('common.message.edit_success'))
          this.$refs.drawer.close()
          this.formValue = {}
          this.getData()
        } else {
          this.$Message.warning(this.$t('common.message.edit_fail'))
        }
      })
    },
    // 打开编辑模态框
    EditArea (root, node, data) {
      console.log(root, node, data)
      this.title = this.$t_p('edit_area_title')
      this.flag = 1
      this.id = data.id
      this.formAdd = {
        orgName: data.orgName,
        parentId: data.parentId,
        oldNode: data.parentId,
        alias: data.alias,
        data: data,
        root: root,
        node: node
      }
      this.modal1 = true
    },
    // 新增下级区域
    okNext () {
      this.checkFrom1().then(() => {
        addArea(this.formEdit).then(
          (res) => {
            this.$Message.success(this.$t('common.message.add_success'))
            this.modal = !this.modal
            this.loadNextLevelData(this.formEdit.parent)
          },
          (res) => {
            this.modal = !this.modal
            if (res.code === 1000) {
              this.$Message.warning(res.msg)
            } else {
              this.$Message.warning(this.$t('common.message.add_fail'))
            }
          }
        )
      })
    },
    // 打开新增下级区域模态框
    append (root, node, data) {
      console.log(data)
      this.modal = true
      this.formEdit = {
        orgName: '',
        parentId: data.id,
        alias: ' ',
        parent: data
      }
    },
    // 删除区域
    remove (root, node, data) {
      this.$Modal.confirm({
        title: this.$t('common.text.prompt'),
        width: this.$flexible(450),
        content: this.$t_p('confirm_deletion') + `[${data.orgName}]?`,
        onOk: () => {
          delOrg({ id: data.id }).then(
            (res) => {
              if (res.code === 200) {
                const parentKey = root.find((el) => el === node).parent
                const parent = root.find((el) => el.nodeKey === parentKey).node
                const index = parent.children.indexOf(data)
                parent.children.splice(index, 1)
                this.$Message.success(this.$t('common.message.delete_success'))
              } else if (res.code === 1000) {
                this.$Message.warning(res.msg)
              } else {
                this.$Message.warning(this.$t('common.message.delete_fail'))
              }
            }
          )
        }
      })
    },
    // 展开收缩
    onTreeExpandToggle (node) {
      if (node.loadState == 0) {
        this.loadNextLevelData(node)
        // console.log(1211, this.allData);
      }
    },
    // 初始化获取列表
    getData () {
      this.treeLoading = true
      getAreaList({
        level: 1
      })
        .then((res) => {
          let nodeMap = {}
          this.oldData = treeTransform(res.data || [], (d, level) => {
            // 0-未加载 1-加载中 2-已加载
            let loadState =
              (d.children && d.children.length > 0) || d.hasChild != 1 ? 1 : 0
            let data = {
              title: d.orgName,
              id: d.id,
              expand: false,
              orgName: d.orgName,
              parentId: d.parentId,
              hasChild: d.hasChild,
              loadState: loadState,
              level,
              children: loadState
                ? d.children
                : d.hasChild == 1
                  ? [
                    {
                      title: this.$t('common.message.loading')
                    }
                  ]
                  : []
            }
            data.expand = false
            data._orig = data
            nodeMap[d.id] = data
            this.nodeMap = {
              ...this.nodeMap,
              ...nodeMap
            }
            return data
          })
          this.allData = [...this.oldData]

          this.getIds(this.oldData)
        })
        .finally(() => {
          this.treeLoading = false
        })
    },
    // 点击加载下级区域
    loadNextLevelData (node) {
      node.loadState = 1
      getNextLevelAreaList({
        parentId: node.id
      })
        .then((res) => {
          let nodeMap = {}
          let subItems = treeTransform(res.data || [], (d, level) => {
            // 0-未加载 1-加载中 2-已加载
            let loadState =
              (d.children && d.children.length > 0) || d.hasChild != 1 ? 1 : 0
            let data = {
              title: d.orgName,
              id: d.id,
              expand: false,
              orgName: d.orgName,
              parentId: d.parentId,
              hasChild: d.hasChild,
              loadState: loadState,
              level,
              children: loadState
                ? d.children
                : d.hasChild == 1
                  ? [
                    {
                      title: this.$t('common.message.loading')
                    }
                  ]
                  : []
            }
            data._orig = data
            nodeMap[d.id] = data
            this.nodeMap = {
              ...this.nodeMap,
              ...nodeMap
            }
            return data
          })

          node.children = subItems
          this.nodeMap[`${node.id}`].children = subItems
          this.getIds(subItems)
          // console.log('qll',this.allData)
        })
        .catch(() => {
          node.loadState = 0
        })
    },
    searchData (arr, name) {
      this.newData = []
      arr.filter((item) => {
        if (item.orgName.indexOf(name) >= 0) {
          this.newData.push(item)
        } else {
          if (item.hasChild > 0 && item.loadState == 1) {
            this.searchData(item.children, name)
          }
        }
      })
    },
    // 搜索
    handleSearch () {
      let name = this.formFilter.alias
      if (name == '') {
        this.allData = this.oldData
      } else {
        this.searchData(this.oldData, name)
        this.allData = this.newData
      }
    },
    changeData () {
      this.handleSearch()
    }
  },
  watch: {
    'formFilter.alias': function (newVal) {
      this.handleSearch()
    }
  },
  mounted () {
    this.getData()
    console.log(this.buttonProps)
  }
}
</script>

<style lang="less">
@import '../../style/varient';
.area-manage {
  height: 100%;
  width: 100%;

  .content {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 24px;
    margin-bottom: 10px;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    .add-btn {
      display: flex;
      justify-content: flex-end;
      .iconfont {
        font-size: 12px;
        margin-right: 12px;
      }
      .ivu-btn {
        // border: 1px solid rgba(0, 0, 0, 0.10196078431372549);
        border: none;
        background: #0084FF;
        color: #fff;
        font-size: 14px;
      }
    }

    .content-box {
      width: 100%;
      height: auto;
      border-bottom: 0px;
      margin-top: 12px;

      .content-header {
        height: 36px;
        width: 100%;
        background-color: #f5f6fa;
        // border-bottom: solid 1px #e1e6eb;
        display: flex;

        span {
          color: #969696;
          height: 36px;
          line-height: 36px;
          text-align: left;
        }

        .header-left {
          flex: 1;
          padding-left: 20px;
        }

        .header-right {
          width: 15%;
        }
      }

      .content-content {
        width: 100%;

        .ivu-tree {
          // background: url(../../../static/img/line.png);
          // background-size: 31px;
        }
        .ivu-btn:not([disabled]):hover {
          background: #000 ;
        }

        .ivu-btn:not([disabled]):active {
          background: #000 ;
        }

        .ivu-tree ul.ivu-tree-children {
          border-bottom: 0;

          .ivu-icon-ios-paper-outline:before {
            content: '';
          }

          .ivu-icon-ios-arrow-forward:before {
            content: '\F341';
            font-size: 20px;
          }

          li {
            margin: 0;
            /*display: flex;*/
            // padding: 1px 0;
            // line-height: 40px;
            // margin-left: -90px;
            position: relative;
            // display: flex;
            //  line-height: 32px;
            .ivu-tree-title {
              flex: 1;
              width: 100%;
            }
            .ivu-tree-arrow {
              position: absolute;
              width: 100px;
              text-align: left;
              z-index: 99;
              height: 31px;
              margin-top: 4px;
              line-height: 31px;
              // display: block;
              // width: 100px;
            }

            .li-box {
              // margin-left: 20px;
              display: flex;
              z-index: 0;
              height: 40px;
              line-height: 40px;
              align-items: center;

              .span-title {
                font-size: 12px;
              }

              .span-title-right {
                padding-left: 20px;
                flex: 1;

                .span-title-icon {
                  flex: 1;
                  padding-top: 10px;
                  margin: 0 10px;
                  height: 2px;
                  // border-bottom: 1px dashed #ddd;
                }
              }

              .span-right {
                // width: 196px;
                display: flex;
                /*width: 230px;*/
                font-size: 14px;
                // padding-left: 5px;
                .primary {
                  margin-right: 8px;
                  color: @color-primary;
                  align-items: center;
                }
                .secondary {
                  margin-right: 8px;
                  color: @color-text-secondary;
                  align-items: center;
                }
              }

              // position: absolute;
              // left: 20px;
            }
          }

          // li {
          //   margin: 0;
          //   padding: 2px 0;
          //   color: #5b5b5b;
          //   // .ivu-icon{
          //   //   display: none;
          //   // }
          //   .ivu-tree-children {
          //     border: 0;
          //   }
          //   .ivu-icon-ios-paper-outline:before {
          //     content: "";
          //   }
          //   .ivu-icon-ios-arrow-forward:before {
          //     content: "\F341";
          //     font-size: 24px;
          //   }
          // }
        }

        // .ivu-tree ul:last-child {
        //   border: 0;
        // }
      }
    }
  }
}
.ivu-tree-title-selected, {
  background-color: #000 !important;
  z-index: 99 !important;
}
.ivu-tree-title-selected:hover{
 background-color: #000 !important;
  z-index: 999 !important;
}
.ivu-tree-title:hover{
  background-color: rgba(0, 0, 0, 0.8) !important;
  z-index: 999 !important;
}
</style>



